import { graphql, useStaticQuery } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import React from 'react'
import { useRef } from 'react';
import slider from 'react-slick/lib/slider';
import Slider from 'react-slick/lib/slider';
import SliderNav from './slider/slider-nav';

type Props = {}

function Instagram({}: Props) {
  const slider = useRef()

  const { allStrapiIgPost } = useStaticQuery(graphql`
    query {
      allStrapiIgPost {
        nodes {
          id
          mediaUrl
          thumbnailUrl
          caption
          permalink
          publishedAt
          mediaType
        }
      }
    }
  `)

  return (
    <section className="px-6 py-16 pb-20">
      <div className="container text-center mb-8">
        <h2 className="section-title text-center mb-2">follow us on instagram</h2>
        <a
          href="https://www.instagram.com/advisoryapps/"
          target="_blank"
          className="text-secondary"
        >
          @advisoryapps
        </a>
      </div>
      <div className="relative">
        <Slider {...{
          slidesToShow: 5,
          lazyLoad: true,
          infinite: true,
          arrows: false,
          ref: slider,
          responsive: [{
            breakpoint: 1023,
            settings: {
              slidesToShow: 3,
            }
          }, {
            breakpoint: 639,
            settings: {
              slidesToShow: 2,
            }
          }]
        }}>
          {allStrapiIgPost.nodes.map(({
            mediaUrl,
            caption,
            id,
            thumbnailUrl,
            mediaType,
            permalink,
          }) => (
            <a
              className="p-4"
              key={id}
              href={permalink}
              target="_blank"
            >
              {/* { mediaType !== "VIDEO" && <StaticImage src={mediaUrl} alt={caption} /> } */}
              <img
                className='w-[175px] h-[175px] md:h-[250px] md:w-[250px] object-cover'
                src={mediaType !== "VIDEO" ? mediaUrl : thumbnailUrl}
                alt={caption}
              />
            </a>
          ))}
        </Slider>
        <SliderNav
          className="absolute right-0 top-0 bottom-0 inline-flex items-center p-4 bg-white/30"
          direction="right"
          onClick={() => slider.current.slickNext()}
        />
      </div>
    </section>
  )
}

export default Instagram