import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Layout from "../components/layout"
import Seo from "../components/seo"
import BannerCareer from "../components/banner/career"
import BlockRichText from "../components/block-rich-text"
import Slider from "react-slick/lib/slider"
import { appendDots } from "../components/slider/slider-dots"
import { useRef } from "react"
import SliderNav from "../components/slider/slider-nav"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faChevronDown } from '@fortawesome/free-solid-svg-icons'
import GoogleMap from "../components/google-map"
import JobApplicationForm from "../components/job-application-form"
import 'slick-carousel/slick/slick.css'
import Instagram from "../components/instagram"

function Job({
  title,
  body,
  form,
}) {
  function onClickApply() {
    form.current.scrollIntoView({ behavior: 'smooth' });
  }

  return (
    <div className="px-6">
      <div className="job-post">
        <h3 className="border-b-2 border-primary text-[40px] text-primary font-bold p-6 leading-none">
          {title}
        </h3>
        <BlockRichText className="p-6 space-y-4" data={body.data.body} />
        <div className="p-6 text-center">
          <button className="button-main" onClick={onClickApply}>
            apply
            &nbsp;
            <FontAwesomeIcon className="text-[1rem]" icon={faChevronDown} />
          </button>
        </div>
      </div>
    </div>
  )
}

function Jobs({
  data,
  ...rest
}) {
  const slider = useRef(null);

  if (data.length === 0) {
    return null
  }

  if (data.length === 1) {
    return <Job {...data[0]} />
  }

  const sliderSettings = {
    className: [
      'slider',
      'slider-reversed-dot',
    ].join(' '),

    dots: true,
    arrows: false,
    appendDots,
    slidesToShow: 2,
    responsive: [
      {
        breakpoint: 1023,
        settings: {
          slidesToShow: 1,
        }
      }
    ]
  }

  const sliderNavClassName = [
    'absolute',
    'top-[150px]',
    'text-[100px]',
    'text-primary',
  ]

  return (
    <div className="relative px-5">
      <SliderNav
        className={[...sliderNavClassName, 'left-0'].join(' ')}
        onClick={() => slider!.current.slickPrev()}
        direction="left"
      >
        &lt;
      </SliderNav>
      <Slider ref={slider} {...sliderSettings}>
        { data.map(jobData => (<Job key={`job-${jobData.id}`} {...jobData} {...rest} />)) }
      </Slider>
      <SliderNav
        className={[...sliderNavClassName, 'right-0'].join(' ')}
        onClick={() => slider!.current.slickNext()}
        direction="right"
      >
        &gt;
      </SliderNav>
    </div>
  )
}

function CareerPage() {
  const { allStrapiJob } = useStaticQuery(graphql`
    query {
      allStrapiJob {
        nodes {
          id
          title
          body {
            data {
              body
            }
          }
        }
      }
    }
  `)

  const seo = {
    metaTitle: "join advisoryapps",
  }

  const jobArray = [...Array(6).keys()].map(() => allStrapiJob.nodes[0]);
  const form = useRef(null);

  return (
    <Layout>
      <Seo seo={seo} />
      <BannerCareer />
      <main>
        <section className="container mb-20">
          <Jobs {...{ data: jobArray || allStrapiJob.nodes, form, }} />
        </section>
      </main>
      <Instagram />
      <GoogleMap />
      <JobApplicationForm forwardRef={form} />
    </Layout>
  )
}

export default CareerPage
